@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: $main1;
        color: #fff;
    }
}
