.text48{
  font-size: 3em;
  line-height: 115%;
}

.text40{
  font-size: 2.5em;
  line-height: 115%;
}

.text28{
  font-size: 1.75em;
  line-height: 128%;
}

.text24{
  font-size: 1.5em;
  line-height: 130%;
}

.text20{
  font-size: 1.25em;
  line-height: 130%;
}

.text16{
  font-size: 1em;
  line-height: 131%;
}

.text14{
  font-size: .875em;
  line-height: 121%;
}

.bold-text{
  font-weight: 700;
}

.font2{
  font-family: $font2;
}

.font3{
  font-family: $font3;
}

.primary-text{
  color: $primary;
}

.light-black-text{
  color: rgba($black,.5);
}


@media screen and (max-width: 992px){
  .text48{
    font-size: 1.625em;
  }

  .text28{
    font-size: 1.25em;
  }

  .text16{
    font-size: .875em;
  }
}
