.header{
  background-color: $main1;
  color: #fff;
  position: relative;
  z-index: 100;
  @media screen and (min-width: 993px){
    .container{
      padding-right: 4.375em;
    }
  }
  &__inner{
    height: 4.625em;
    display: flex;
    align-items: center;
  }
  &__item{
    display: flex;
    align-items: center;
    margin-right: 1.875em;
    img{
      height: 2em;
      flex-shrink: 0;
      display: block;
      margin-right: .625em;
    }
    span{
      font-size: .8125em;
      line-height: 138%;
    }
  }
  &__logo{
    display: block;
    width: 10em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__links{
    display: flex;
    align-items: center;
    margin-left: auto;
  }
  &__link{
    margin-left: 3.4375em;
    color: $white50;
    span{
      font-size: .875em;
    }
    &:hover{
      color: #fff;
    }
  }
  &__buttons{
    display: flex;
    align-items: center;
    position: absolute;
    left: 0;
    top: 0;
  }
  &._open{
    .menu{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._menu-open{
    ._view1{
      display: block;
    }
  }
  &._search-open{
    ._view2{
      display: block;
    }
  }
}

.burger{
  height: 4.625em;
  width: 4.625em;
  @include center;
  svg,
  img{
    width: 2.5em;
    height: auto;
    display: block;
  }
}

.search-toggle{
  padding: 1.0625em;
  img{
    width: 100%;
    display: block;
  }
}

.menu{
  position: fixed;
  top: 0;
  left: 0;
  background-color: $main1;
  z-index: 100;
  width: 100%;
  padding: 13.5625em 0 0;
  height: $fullHeight;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &:before{
    content: '';
    position: fixed;
    background: $white10;
    top: 0;
    left: 4.5625em;
    width: 1px;
    height: $fullHeight;
    transform: translateX(-1px);
  }
  &__view{
    display: none;
  }
  &__grid{
    display: flex;
  }
  &__links,
  &__links2{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__links{
    margin-right: 9.5625em;
  }
  &__link,
  &__link2{
    color: $white50;
    position: relative;
    transition: all .3s;
    span{
      line-height: 130%;
    }
    &._active,
    &:hover {
      color: #fff;
    }
  }
  &__link{
    font-weight: 700;
    @include lastMb(2.8125em);
    &:after{
      content: '';
      position: absolute;
      top: 1.125em;
      left: 100%;
      margin-left: .9375em;
      width: 2em;
      height: .125em;
      background-color: #fff;
      transition: inherit;
      opacity: 0;
    }
    span{
      font-size: 1.5em;
    }
    &._active,
    &:hover{
      &:after{
        opacity: 1;
      }
    }
  }
  &__link2{
    @include lastMb(3.125em);
    span{
      font-size: 1.25em;
    }
  }
}

._close-btn{
  position: fixed;
  top: 0;
  left: 0;
  background: #FFFFFF;
  width: 4.5625em;
  height: 4.5625em;
  @include center;
  &:hover{
    background: $gray;
  }
  img{
    width: 2.5em;
    display: block;
  }
}

.header-search{
  &__links{
    max-width: 43.5em;
    margin-top: 2.8125em;
    column-count: 2;
    column-gap: 1em;
  }
  .menu__link2{
    display: inline-block;
    width: 100%;
    @include lastMb(1.875em);
  }
  &__field{
    margin-top: -7.5em;
    margin-bottom: 3.75em;
    max-width: 32.0625em;
    position: relative;
    input{
      width: 100%;
      background-color: transparent;
      font-size: 1em;
      line-height: 131.25%;
      padding: 0 .9375em;
      border: 1px solid $white50;
      height: 3.6875em;
      color: #fff;
      &::placeholder{
        color: $white50;
      }
      &:focus{
        border-color: #fff;
      }
    }
    &-btn{
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      opacity: .5;
      padding: 1.25em;
      &:hover{
        opacity: 1;
      }
      img{
        width: 1.125em;
        display: block;
      }
    }
  }
}

.menu-content{
  flex-grow: 1;
  padding-bottom: 2em;
}

.menu-footer{
  margin-top: auto;
  padding: 1.625em 0;
  border-top: 1px solid $white10;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__phone,
  &__address{
    display: flex;
    align-items: center;
    margin-right: 1.5625em;
    img{
      flex-shrink: 0;
      margin-right: .375em;
      display: block;
      width: 1.25em;
    }
  }
  &__buttons{
    display: flex;
    align-items: center;
  }
  &__btn{
    width: 1.25em;
    flex-shrink: 0;
    margin-right: 1.125em;
    &:hover{
      opacity: .8;
    }
    img{
      width: 100%;
      display: block;
    }
  }
  &__support{
    margin-left: auto;
    display: flex;
    align-items: center;
    img{
      margin-left: .625em;
      width: 8.625em;
      display: block;
      flex-shrink: 0;
    }
  }
}


@media screen and (min-width: 993px){
  @media screen and (max-height: 860px){
    .menu{
      padding-top: 6em;
    }

    .header-search{
      &__field{
        margin-bottom: 2.75em;
        margin-top: 0;
      }
    }
  }

  .header{
    &._index{
      position: absolute;
      width: 100%;
      left: 0;
      top: 0;
      background-color: transparent;
      &:after{
        content: '';
        position: absolute;
        top: 0;
        width: 4.625em;
        height: $fullHeight;
        background-color: $primary;
        background-image: url(../img/arrow-bottom.svg);
        background-repeat: no-repeat;
        background-position: center bottom 1.125em;
        background-size: 2.25em;
      }
      .burger{
        background-color: #fff;
        z-index: 2;
        path{
          stroke: $primary;
        }
      }
      .search-toggle{
        z-index: 2;
        position: absolute;
        padding: 1.375em;
        width: 4.625em;
        left: 0;
        top: 4.625em;
        @include center;
        img{
          width: 1.125em;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      height: 4em;
    }
    &__logo{
      width: 8.5em;
    }
    &__links,
    &__item{
      display: none;
    }
    &__buttons{
      position: static;
      margin-left: auto;
    }
    &._open{
      .menu{
        transform: translateX(0%);
      }
    }
  }

  .burger{
    margin-right: -.5em;
    width: unset;
    height: unset;
    order: 2;
    background-color: transparent !important;
  }

  .menu{
    padding: 4em 0 1em;
    transform: translateX(-200%);
    &:before{
      display: none;
    }
    &__grid{
      display: block;
    }
    &__links{
      margin: 0 0 2em;
    }
    &__link2{
      @include lastMb(1.25em);
      span{
        font-size: 1.125em;
      }
    }
    &__link{
      @include lastMb(1.25em);
      span{
        font-size: 1.25em;
      }
      &:after{
        display: none;
      }
    }
  }

  ._close-btn{
    width: 3em;
    height: 3em;
    left: unset;
    right: 0;
    img{
      width: 3em;
    }
  }

  .header-search{
    &__field{
      margin-top: 0;
      margin-bottom: 2em;
      input{
        height: 3em;
      }
    }
    &__links{
      column-count: 1;
      margin-top: 1.5em;
      .menu__link2{
        @include lastMb(1.25em);
      }
    }
  }

  .menu-footer{
    &__inner{
      display: block;
    }
    &__address,
    &__phone{
      margin: 0 0 1.25em;
    }
    &__support{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .menu-footer{
    &__support{
      display: block;
      img{
        margin: .625em 0 0;
      }
    }
  }
}
