.main{
  background-image: url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  position: relative;
  color: #FFFFFF;
  &:before{
    content: '';
    position: absolute;
    inset: 0;
    background: rgba(15, 29, 64, 0.9);
  }
  &__inner{
    min-height: $fullHeight;
    padding: 12.5em 0 19.375em;
    position: relative;
    z-index: 2;
    display: flex;
    align-items: flex-end;
  }
  &__content{
    @include grid100gap(1.25em);
    max-width: 50.5em;
  }
  &__badges{
    display: flex;
    margin-bottom: .3125em;
  }
  &__badge{
    @include center;
    padding: 0 .625em;
    border: 1px solid $white50;
    height: 3.3125em;
    color: $white50;
    img{
      width: 1.5em;
      margin-right: .3125em;
      display: block;
      flex-shrink: 0;
    }
    span{
      opacity: .8;
    }
  }
  &__text{
    letter-spacing: 0.02em;
  }
}

.main-footer{
  z-index: 2;
  position: absolute;
  left: 0;
  bottom: 0;
  padding-left: 4.625em;
  width: 100%;
  display: grid;
  grid-template-columns: 36.237% 40.63% 23.133%;
  &__video{
    height: 100%;
    width: 100%;
    @include center;
    text-align: center;
    text-transform: uppercase;
    font-weight: 700;
    background-image: url(../img/line.webp);
    background-repeat: no-repeat;
    background-position: right 0 top .875em;
    background-size: auto 7.9375em;
    span{
      font-size: 1.375em;
      line-height: 137%;
    }
  }
}

.main-link{
  height: 11.875em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 4.375em 0 1.75em;
  background: rgba($main1,.75);
  backdrop-filter: blur(2px);
  &:hover{
    background: rgba($main1,.95);
    color: #fff;
  }
  &__arrow{
    position: absolute;
    right: 0;
    top: 0;
    width: 1.875em;
    height: 100%;
    @include center;
    background-color: $main1;
    img{
      width: 100%;
    }
  }
  &__info{
    @include grid100gap(.1875em);
    max-width: 16.25em;
    &:before{
      content: '';
      display: block;
      width: 1.4375em;
      height: .125em;
      background-color: #fff;
      margin-bottom: .75em;
    }
  }
  &__title{
    text-transform: uppercase;
  }
  &__nums{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $white50;
    .text28{
      color: #fff;
    }
  }
}

.edu{
  padding: 9.375em 0;
  &__grid{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 1.5625em);
    margin-top: 1em;
  }
}

.edu-card{
  min-height: 16.4375em;
  background-color: $gray;
  width: calc(50% - 1.5625em);
  margin: 1.5625em 1.5625em 0 0;
  display: flex;
  align-items: flex-end;
  position: relative;
  @media screen and (min-width: 993px){
    &._bachelor{
      width: calc(65.886% - 1.5625em);
      .edu-card__img{
        width: 24.125em;
      }
    }
    &._master{
      width: calc(100% - 65.886% - 1.5625em);
    }
  }
  &._master{
    color: #fff;
    background-color: $red;
    .edu-card__img{
      right: 0;
      top: 0;
      position: absolute;
      height: 100%;
      width: unset;
      img{
        height: 100%;
        width: auto;
        display: block;
      }
    }
    .edu-card__faculty-title{
      &:before{
        background-color: #fff;
      }
    }
  }
  &__content{
    padding: 2.1875em 2.5em;
    position: relative;
    z-index: 2;
    max-width: 20em;
    min-height: 100%;
    display: flex;
    flex-direction: column;
  }
  &__info{
    @include grid100gap(.3125em);
  }
  &__faculty{
    margin-top: 1.25em;
    @include grid100gap(.5em);
    &-title{
      display: flex;
      align-items: center;
      &:before{
        content: '';
        display: block;
        flex-shrink: 0;
        width: 2.3125em;
        height: .125em;
        background-color: $black;
        margin-right: .5em;
      }
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__btn{
    height: 2.25em;
    padding: 0 .9375em;
    span{
      font-size: .875em;
    }
  }
  &__img{
    width: 16.3125em;
    margin-left: auto;
    img{
      width: 100%;
      display: block;
    }
  }
  &__icon{
    height: 3.0625em;
    margin-bottom: auto;
    img{
      display: block;
      height: 100%;
    }
  }
}


@media screen and (min-width: 993px) {
  @media screen and (max-height: 860px) {
    .main{
      &__inner{
        padding: 10em 0 15em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    &__inner{
      padding: 2em 0;
      min-height: unset;
    }
    &__content{
      gap: 1em;
    }
  }

  .main-footer{
    padding: 0;
    position: relative;
    grid-template-columns: 100%;
    &__video{
      height: 3.5em;
      br{
        display: none;
      }
    }
  }

  .main-link{
    height: unset;
    padding: 1.25em 1.25em 3em;
    &__arrow{
      width: 100%;
      height: 1.875em;
      bottom: 0;
      top: unset;
      img{
        width: 1.875em;
        transform: rotate(90deg);
      }
    }
  }

  .edu{
    padding: 2em 0;
    .text28{
      font-size: 1.625em;
    }
    &__grid{
      width: 100%;
      margin: 0;
    }
  }

  .edu-card{
    width: 100%;
    margin-right: 0;
    min-height: unset;
    &__content{
      padding: 1.5em 1em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__nav{
      margin-top: 1em;
    }
    &__icon{
      margin-bottom: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-footer{
    &__video{
      span{
        font-size: 1em;
      }
    }
  }

  .main-link{
    &__nums{
      display: none;
    }
  }

  .edu-card{
    &__img{
      display: none;
    }
  }
}
